.Loginbody {
  height: 100%;
  min-height: 100vh;
  width: 100%;
  background-color: #2f4858;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Loginbox {
  background-color: #f5f5f5;
  width: 320px;
  align-items: center;
  justify-content: center;
  display: block;
  border-radius: 20px;
  color: #333;
  padding: 30px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
}

input {
  display: block;
  margin: 10px auto;
  width: 100%;
  height: 40px;
  border: 2px solid #4f93a5;
  caret-color: #4f93a5;
  padding: 10px;
  border-radius: 8px;
  background-color: #f9f9f9;
  color: #333;
  font-size: 16px;
  transition: border-color 0.3s;
}

.Inputdiv {
  margin: 15px 0;
}

.button {
  width: 100%;
  height: 40px;
  border: none;
  background-color: #4f93a5;
  color: white;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s;
}

.button:hover {
  background-color: #36677a;
}

input:focus {
  outline: none;
  border-color: #9ec6d0;
}

.Loginbody label {
  font-size: 18px;
  color: #4f93a5;
}

.Link {
  text-decoration: none;
  color: #4f93a5;
  transition: color 0.3s;
}

.Link:hover {
  color: #36677a;
}

.error {
  color: #e74c3c;
  font-size: 14px;
}
