.header {
  display: flex;
  background-color: #2a4365;
  align-items: center;
  font-size: 24px;
  justify-content: space-between;
  padding: 1rem 5%;
  color: white;
}

.Link {
  font-size: 18px;
  text-decoration: none;
  color: #2a4365;
  background-color: white;
  border: 1px solid white;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  margin: 0.5rem;
  transition: background-color 0.3s, color 0.3s;
}

.Link:hover {
  background-color: #2a4365;
  color: white;
}

.User {
  text-decoration: none;
  height: 40px;
  width: 40px;
  border: 2px solid #2a4365;
  border-radius: 100%;
  margin: 0.5rem;
}

.Useroptions {
  display: flex;
  align-items: center;
}

.OutPro {
  display: flex;
  align-items: flex-end;
}

.main {
  padding: 2rem 5%;
}
