.Signupbody {
  height: 100vh;
  width: 100%;
  background-color: #2f4858;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Signupbox {
  background-color: #ffffff;
  width: 400px;
  border-radius: 15px;
  padding: 30px;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
}

input {
  display: block;
  width: 100%;
  padding: 10px;
  border: none;
  border-radius: 5px;
  margin-bottom: 15px;
  background-color: #f0f0f0;
  color: #333333;
}

.Inputdiv {
  margin-bottom: 20px;
}

.button {
  width: 100%;
  padding: 10px;
  background-color: #4f93a5;
  color: #ffffff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.button:hover {
  background-color: #36677a;
}

input:focus {
  outline: none;
  border: 2px solid #ff5733;
}

.Signupbox h1 {
  font-size: 28px;
  margin-bottom: 20px;
  color: #333333;
}

.Link {
  text-decoration: none;
  color: #4f93a5;
  font-weight: bold;
}

.error {
  color: #ff0000;
  font-size: 14px;
  margin-top: 10px;
}
