.body {
  color: white;
  background-color: #1c2b40;
  padding: 20px;
}

.Uploadside {
  justify-content: center;
  margin: 2vh;
  background-color: #007f04;
  border: 1px solid #007f04;
  color: white;
  font-size: 18px;
  padding: 10px 20px;
  border-radius: 5px;
  transition: background-color 0.3s;
  cursor: pointer;
}

.Uploadside:hover {
  background-color: #ffffff;
  color: #007f04;
}

.Box {
  text-align: center;
  background-color: #2a4365;
  width: 60%;
  margin: 10vh auto;
  padding: 5vh;
  border-radius: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
}

.Uploadchoose {
  background-color: #444;
  color: #efefef;
  border: none;
  padding: 8px 12px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.Uploadchoose:hover {
  background-color: #333;
}

.Uploadedfiles {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10vh;
}

.Filebutton {
  margin: 2vh;
  background-color: #457b9d;
  color: white;
  font-size: 15px;
  padding: 10px 20px;
  border-radius: 5px;
  transition: background-color 0.3s;
  cursor: pointer;
}

.Filebutton:hover {
  background-color: #234e6d;
}

.createFolder {
  text-align: center;
  background-color: #3c5cb5;
  width: 40%;
  margin: 5vh auto;
  padding: 2vh;
  border-radius: 15px;
  transition: background-color 0.3s;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
}

.createFolder:hover {
  background-color: #234e6d;
}

.createdFoldersStyle {
  text-align: center;
  background-color: #6d597a;
  width: 60%;
  margin: 5vh auto;
  padding: 3vh;
  border-radius: 20px;
  transition: background-color 0.3s;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
}

.createdFoldersStyle:hover {
  background-color: #57425d;
}
